.page-main__titles {
  font-family: "OpenSans-Light", Verdana, Geneva, Tahoma, sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 50px;
  line-height: 68px;
  width: 280px;
  margin: 0 auto;
  padding-top: 40px;
  padding-bottom: 31px;
  @media (min-width: @tablet-width) {
    width: 588px;
    padding-top: 45px;
    padding-bottom: 25px;
  }
  @media (min-width: @desktop-width) {
    width: 780px;
    padding: 78px 10px 47px;
  }
}

.page-main__title {
  margin: 0 auto;
  font-size: 24px;
  font-weight: 300;
  line-height: 28px;
  text-align: center;
  &:last-child {
    margin-left: auto;
    margin-right: auto;
    padding-top: 10px;
    width: 75%;
    @media (min-width: @tablet-width) {
      padding: 0;
    }
  }
  &:last-child br {
    display: none;
  }
  &--first {
    margin-bottom: 20px;
  }
  @media (min-width: @tablet-width) {
    font-size: 40px;
    line-height: 54px;
    &--first {
      margin-bottom: 0;
    }
  }
  @media (min-width: @desktop-width) {
    font-size: 50px;
    line-height: 68px;
  }
}

.page-main {
  @media (min-width: @desktop-width) {
    margin-top: -15px;
  }
}


/*photo*/

.page-main__wrapper {
  width: 250px;
  margin: 0 auto;
  text-align: center;
  padding-top: 29px;
  padding-bottom: 37px;
  @media (min-width: @tablet-width) {
    width: 55%;
    padding-top: 73px;
    padding-bottom: 60px;
  }
  @media (min-width: @desktop-width) {
    width: 47%;
    padding-top: 89px;
    padding-bottom: 70px;
  }
}

.page-main__slogan {
  font-size: 14px;
  line-height: 30px;
  @media (min-width: @tablet-width) {
    font-size: 18px;
    line-height: 30px;
  }
}
