.social {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  margin-bottom: 35px;
  align-items: center;
  @media (min-width: @tablet-width) {
    margin-bottom: 0;
  }
  @media (min-width: @desktop-width) {
    margin-left: 15px;
  }
}

.social__item {
  display: flex;
  margin-right: 8px;
  width: 48px;
  height: 48px;
  border: 3px solid fade(@dark-blue-gray, 10%);
  border-radius: 50px;
  cursor: pointer;
  &:hover {
    border: 3px solid @dark-blue-gray;
    transition: 0.5s;
  }
  &:active {
    opacity: 0.1;
  }
}

.social__link {
  margin: auto auto;
}

.social__link svg {
  fill: #D22856;
}
