.site-list {
  margin: 0;
  padding: 0;
  font-family: "OpenSans-Bold", Verdana, Geneva, Tahoma, sans-serif;
  font-size: 18px;
  line-height: 32px;
  list-style: none;
  text-transform: uppercase;
  text-align: center;
  @media (min-width: @desktop-width) {
    font-family: "OpenSans-Light", Verdana, Geneva, Tahoma, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 18px;
    text-transform: none;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
}

.site-list__item {
  border-bottom: 1px solid #000000;
  border-top: 1px solid #485461;
  padding: 15px 20px;
  @media (min-width: @desktop-width) {
    margin-top: 15px;
    border: none;
    padding-right: 63px;
    &:last-child {
      padding-right: 3px;
    }
  }
}

.site-list__link {
  text-decoration: none;
  color: @white;
  font-weight: bold;
  &:hover {
    color: @pink;
    transition: 0.1s;
  }
  &:active {
    color: fade(@pink, 30%);
  }
  @media (min-width: @desktop-width) {
    padding-bottom: 9px;
    &--active {
      border-bottom: 2px solid @white;
    }
  }
}

@media (min-width: @desktop-width) {
  .site-list__link {
    &:hover {
      color: @pink;
      transition: 0.1s;
    }
    &:active {
      color: rgba(@white, 0.3);
    }
  }
}
